import React from 'react'
import "../Styles/Contact.scss"
import image from '../assets/home4.jpg'
const Contact = () => {
  return (
    <div className='Contact'>
      <h1>Kapcsolat!</h1>
      <div className="container">

        <div className="image"><img src={image} alt="" /></div>


        <form >
          <h3>Kldj neknk zenetet</h3>
          <label name="nev">Nv</label>
          <input type="text" name='nev' placeholder='nv'  autoComplete="off"/>
          <label name="email">Email</label>
          <input type="email" name="email" id="" placeholder='email' />
          <label name="tel">Telefonszm</label>
          <input type="tel" name="tel" id="" placeholder='telefonszm'  autoComplete="off"/>
          <label name="message">zenet</label>
          <textarea  autoComplete="off" name="message" id="" cols="10" rows="4" placeholder='"zenet'></textarea>
          <div className='checkbox'>
            <input type="checkbox" id="scales" name="scales" />
              <label htmlFor="scales">Elfogadom az adatkezelsi feltteleket</label>
          </div>
          <button>Klds</button>
        </form>

      </div>

    </div>
  )
}

export default Contact